<template>
    <div class="home">
        <elm-header :fatherName="fatherName"></elm-header>
        <div class="home-nav">
            <mt-swipe :auto="5000">
                <mt-swipe-item>
                    <ul>
                        <li v-for='(item,index) in headerNav' :key="index" @click="jumpRsetaurant">
                            <img :src="item.img" alt=""/>
                            <p>{{item.name}}</p>
                        </li>
                    </ul>
                </mt-swipe-item>
                <mt-swipe-item>
                        <ul>
                                <li v-for='(item,index) in headerNav2' :key="index">
                                    <img :src="item.img" alt="" />
                                    <p>{{item.name}}</p>
                                </li>
                            </ul>
                </mt-swipe-item>
            </mt-swipe>
        </div>
        <div class="restaurant">
            <h1>附近商家</h1>
            <restaurant-list></restaurant-list>
        </div>
        <elm-footer></elm-footer>
    </div>
</template>

<script>
import restaurantList from "../components/restaurant-list"
export default {
    name:"home",
    data () {
        return {
           msg:"测试数据" ,
           fatherName:null,
           headerNav:[
               {"name":"美食","img":"../../static/img/ms-01.jpg"},
               {"name":"甜品饮品","img":"../../static/img/ms-02.jpg"},
               {"name":"商品超市","img":"../../static/img/ms-03.jpg"},
               {"name":"预定早餐","img":"../../static/img/ms-04.jpg"},
               {"name":"果蔬生鲜","img":"../../static/img/ms-02.jpg"},
               {"name":"新店特惠","img":"../../static/img/ms-03.jpg"},
               {"name":"准时达","img":"../../static/img/ms-04.jpg"},
               {"name":"夜宵","img":"../../static/img/ms-01.jpg"}
           ],
           headerNav2:[
               {"name":"美食","img":"../../static/img/ms-01.jpg"},
               {"name":"甜品饮品","img":"../../static/img/ms-02.jpg"},
               {"name":"商品超市","img":"../../static/img/ms-03.jpg"},
               {"name":"预定早餐","img":"../../static/img/ms-04.jpg"},
               {"name":"果蔬生鲜","img":"../../static/img/ms-02.jpg"},
               {"name":"新店特惠","img":"../../static/img/ms-03.jpg"},
               {"name":"准时达","img":"../../static/img/ms-04.jpg"},
               {"name":"夜宵","img":"../../static/img/ms-01.jpg"}
           ]
        }
    },
    components:{
        restaurantList
    },
    created () {
       this.fatherName=this.$route.query.id;
    },
    methods: {
        jumpRsetaurant:function(){
            this.$router.push("/restaurant");
        }
    }
}
</script>

<style scoped>
.home-nav{
    width:100%;
    height:3.4rem;
    background: #fff;
    padding:0.3rem 0.2rem;
}
.home-nav ul{
    display: flex;
    justify-content: center;
    align-items: center;
    flex-wrap: wrap;
} 
.home-nav ul li{
    flex:25%;
    text-align: center;
    padding-bottom: 0.06rem;
}

.home-nav ul li img{
    width:.8rem;
    height:.8rem;
}
.home-nav ul li p{

    font-size:0.26rem;
    color:#555;
}

.restaurant{
    margin-top:0.2rem;
    margin-bottom:1rem;
    background: #fff;
    padding:0.2rem 0;
}
.restaurant h1{
    font-size:0.26rem;
    color:#555;
    text-indent: 0.2rem;
}
</style>


