import Vue from 'vue'
import Router from 'vue-router'
import location from '@/page/location'
import locationCity from "@/page/locationCity"


import home from '@/page/home'
import search from '@/page/search'
import order from '@/page/order'
import user from '@/page/user'
import login from '@/page/login'
import restaurant from '@/page/restaurant'
import restaurantIntr from '@/page/restaurant-intr'
import restaurantItem from '@/page/restaurant-item'
import food from '@/page/food'

import payment from '@/page/payment'
import pay from '@/page/pay'
import addAddress from '@/page/addAddress'
import remarks from "@/page/remarks"
import invoice from "@/page/invoice"

import account from "@/page/account/account"
import accountPicture from "@/page/account/account-picutre"
import accountName from "@/page/account/account-name"
import accountPassword from "@/page/account/account-password"
import accountTel from "@/page/account/account-tel"
import accountCancel from "@/page/account/account-cancel"
import accountAPP from "@/page/account/account-app"
import integral from "@/page/account/integral"
import vip from "@/page/account/account-vip"
import service from "@/page/account/account-service"



import lx from '@/page/lx'


Vue.use(Router)

export default new Router({
  routes: [
    {
      path:"/",
      name:'location',
      component:location
    },
    {
      path:"/city",
      name:"locationCity",
      component:locationCity
    },
    {
      path: '/home',
      name: 'home',
      component: home,
    },
    {
      path: '/search',
      name: 'search',
      component: search
    },
    {
      path:'/order',
      name:'order',
      component: order
    },
    {
      path:'/user',
      name:'user',
      component: user
    },
    {
      path:'/login/:login_parameter',
      name:'login',
      component:login
    },
    {
      path:"/restaurant",
      name:"restaurant",
      component:restaurant
    },
    {
      path:"/restaurant-intr",
      name:"restaurantIntr",
      component:restaurantIntr

    },
    {
      path:"/food",
      name:"food",
      component:food
    },
    {
      path:"/restaurantitem/:id",
      name:"restaurantitem",
      component:restaurantItem
    },
    {
      path:"/payment",
      name:"payment",
      component:payment,
    },
    {
      path:"/pay",
      name:"pay",
      component:pay,
    },
    {
      path:"/lx",
      name:"lx",
      component:lx,
    },
    {
      path:"/addAddress",
      name:"addAddress",
      component:addAddress,
    },
    {
      path:"/invoice",
      name:"invoice",
      component:invoice,
    },
    {
      path:"/remarks",
      name:"remarks",
      component:remarks,
    },
    {
      path:"/account",
      name:"account",
      component:account,
    },
    {
      path:"/account/picture",
      component:accountPicture,
    },
    {
      path:"/account/name",
      component:accountName,
    },
    {
      path:"/account/password",
      component:accountPassword,
    },
    {
      path:"/account/tel",
      component:accountTel,
    },
    {
      path:"/account/cancel",
      component:accountCancel,
    },
    {
      path:"/account/app",
      component:accountAPP
    },
    {
      path:"/integral",
      name:"integral",
      component:integral
    },
    {
      path:"/vip",
      name:"vip",
      component:vip,
    },
    {
      path:"/service",
      name:"service",
      component:service,
    }

  ]
})



