<template>
    <div class="remarks">
        <elm-header :fatherName="fatherName"></elm-header>
        <div class="remarks-main">
            <textarea name="" id="" v-model="userInput" @input="userInputRemarks"  placeholder="请输入口味、偏好等要求，不支持额外代购商品哦~"></textarea>
            <span>0/50</span>
            <div>
                <h1>
                    <span class="remarks-list-titel">快捷标签</span>
                    <span>编辑</span>
                </h1>
                <p class="remarks-list">
                    <span v-for="(item, index) in remarksList" :key="index" @click="chooseRemark(index,$event)" >
                        {{item}}
                    </span>
                </p>
                <mt-button type="primary" size="large" @click="sendChooseRemark">保存</mt-button>
            </div>
        </div>
    </div>
</template>

<script>
import { mapState,mapMutations } from "vuex"
export default{
    name:"remarks",
    data () {
        return {
            fatherName:"添加备注",
            isActive:true,
            userInput:null,
            remarksList:["不吃辣","少放辣","多放辣","不吃蒜","少醋","多醋","不吃香菜","今天我生日，可不可以要惊喜！"],
            chooseItem:[],
        }
    },
    computed: mapState(['userOrderRemarks']),
    methods: {
        ...mapMutations(['Choose_OrderRemarks']),
        chooseRemark:function(index,$event){           
            if ($event.target.className) {
                $event.target.className="";    
                this.chooseItem.splice(this.chooseItem.indexOf(this.remarksList[index]),1)
            }else{
                $event.target.className="active";
                this.chooseItem.push(this.remarksList[index]);
            }
            this.userInput =this.chooseItem.join(",");
            //console.log(this.chooseItem)
        },
        userInputRemarks:function(){
            this.chooseItem.push(this.userInput)
        },
        sendChooseRemark:function(){
            this.$router.push("/payment");
        }   
    },
    watch: {
        userInput:function(val){
            this.Choose_OrderRemarks(val);
        }
    }
}

</script>

<style scoped>
.remarks{
    background: #fff;
    overflow: hidden;
}
.remarks-main{
    height:13rem;
    position: relative;
    padding:0.3rem 0.2rem;
}
.remarks-main>textarea{
    width:100%;
    height:3rem;
    padding:0.2rem;
    box-sizing: border-box;
}
.remarks-main>span{
    position: absolute;
    right:0.3rem;
    top:2.9rem;
    font-weight: bold;
} 
.remarks-main h1{
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding:0.4rem 0;
}
.remarks-main h1 span{
    font-size:0.3rem;
}
.remarks-list-titel{
    color:#999;
}
.remarks-list{
    font-size:0;
    padding-bottom:0.6rem;
}
.remarks-list span{
    display: inline-block;
    padding:0.1rem;
    border:1px solid #e6e6e6;
    margin:0 0.3rem 0.3rem 0;
    font-size:0.2rem;
}
.remarks-list span.active{
    background: orange;
    color:#fff;
}
</style>


