                <img src="../../assets/img/erm.png" alt="" />
                <p>扫一扫上面的二维码图片，下载APP吧！</p>

        </div>
        <elm-footer></elm-footer>
    </div>

</template>
<script>
export default{
    name:"accountAPP",
    data () {
        return {
            fatherName:"下载APP",
        }
    }
}

</script>


<style scoped>
.account-app-main{
    padding-top:2rem;
    text-align: center;
}
.account-app-main h1{
    font-size:0.36rem;
    color:orange;
    letter-spacing: 0.04rem;
}
.account-app-main img{
    display: block;
    width:3.14rem;
    height:3.2rem;
    margin:0.3rem auto;
    
}
.account-app-main p{
    font-size:0.3rem;
    color:#999;
    letter-spacing: 0.02rem;
}
</style>


