<template>
    <div class="restaurant-list">
        <!-- 在编写页面布局时选择了felx嵌套，很好用 -->
        <ul>
            <template v-for="n in 2">
                <li @click="jumpRestaurantItem(index)" v-for="(item, index) in restaurantList" >
                    <img :src="item.img" alt="" />
                    <div>
                        <h1>
                            <span class="restaurant-list-brand" v-if="item.brand">品牌</span>
                            <span class="restaurant-list-title">{{item.title}}</span>
                            <span class="restaurant-list-service">
                                <template v-if="item.service">
                                    <i>保</i>
                                    <i>准</i>
                                </template>
                                <template v-else>
                                    <i>票</i>
                                </template>
                            </span>
                        </h1>
                        <h2>
                            <span>
                                <span class="restaurant-list-stars">
                                    <i class="iconfont icon-xingxing"></i>
                                    <i class="iconfont icon-xingxing"></i>
                                    <i class="iconfont icon-xingxing"></i>
                                    <i class="iconfont icon-xingxing"></i>
                                    <i class="iconfont icon-xingxing"></i>
                                </span>
                                <span class="restaurant-list-level">{{item.level}}</span>
                                <span class="restaurant-list-salesVolume">月销售{{item.salesVolume}}单</span>
                            </span>
                            <span>
                                <span v-if="item.isFengNiao" class="restaurant-list-fengniao">蜂鸟转送</span>
                                <span v-if="item.onTime" class="restaurant-list-ontime">准时达</span>
                            </span>
                        </h2>
                        <div class="restaurant-list-mask">
                            <p>
                                <span class="restaurant-list-minConsumption">{{item.minConsumption}}元起 /</span>
                                <span class="restaurant-list-delivery" v-if="delivery">配送费￥{{item.delivery}}</span>
                                <span class="restaurant-list-free" v-else>免费配送</span>
                            </p>
                            <p>
                                <span class="restaurant-list-time">{{item.distance}}米 / <i>{{item.time}}分钟</i></span>
                            </p>
                        </div>
                    </div>
                </li>
            </template>
        </ul>
    </div>
</template>

<script>
import {mapState} from "vuex"
export default{
    name:"restaurantList",
    data() {
        return {
            level:4,
            salesVolume:3978, 
            delivery:6,
            minConsumption:20,
            distance:789,
            time:37,
            brand:true,
            service:true,
            
        }
    },
    computed: mapState(['restaurantList']),
    methods: {
        jumpRestaurantItem:function(index){
            this.$router.push({
                path:`/restaurantitem/${index}`
            });
        }
    }
}

</script>


<style scoped>
.restaurant-list{
    padding:0.2rem 0.16rem 0.4rem;
    background: #fff;
}

.restaurant-list ul li{
    display: flex;
    align-items: center;
    padding:0.4rem 0 0.3rem;
    border-bottom:1px solid #e2dfdf;
}
.restaurant-list img{
    width:1.3rem;
    height: 1.3rem;
    margin-right:0.2rem;
}
.restaurant-list div{
    flex:68%;
}
.restaurant-list h1{
    display: flex;
    justify-content: space-between;
    align-items: center;
    font-size: 0;

}
.restaurant-list-brand{
    font-size:0.24rem;
    font-weight: bold;
    color:black;
    padding:0.06rem 0.08rem;
    background: orange;
    line-height: 0.3rem;
    margin-right:0.1rem;
    border-radius: 0.04rem;
}
.restaurant-list-title{
    flex:59%;
    font-size:0.3rem;
    font-weight: bold;
    color:black;
}
.restaurant-list-service i{
    display: inline-block;
    border:1px solid #999;
    font-size:0.20rem;
    color:#999;
    font-style: normal;
    padding:0 0.04rem;
    margin-right:0.06rem;
}
.restaurant-list h2{
    display: flex;
    justify-content: space-between;
    margin-top:0.16rem;
    font-size:0;
}
.restaurant-list h2 span{
    font-size:0.2rem;
}

.restaurant-list h2 span.restaurant-list-stars{
    display: inline-block;
    width:1.16rem;
    font-size:0;
}
.restaurant-list-stars i{
    font-size:0.22rem;
}
.restaurant-list-level{
    font-size:0.24rem;
    color:orange;
    margin-right:0.1rem;
}
.restaurant-list-salesVolume{
    font-size:0.22rem;
}
.restaurant-list-mask{
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin-top:0.16rem;
}
.restaurant-list p span{
    font-size:0.22rem;
}
.restaurant-list-time i{
    font-size:0.22rem;
    font-style: normal;
    color:rgb(16, 166, 204);
}
.restaurant-list-fengniao{
    color:#fff;
    background: rgb(16, 166, 204);
    border-radius: 0.04rem;
    padding:0.04rem 0.08rem;
}
.restaurant-list-ontime{
    border:1px solid rgb(16, 166, 204);
    border-radius: 0.04rem;
    padding:0.02rem 0.04rem;
}
</style>


