<template>
    <div class="elm-footer">
       <ul>
           <router-link 
                :to="item.href" tag="li"  
                v-for='(item,index) in menu' 
                :key="index" 
                exact-active-class="active">
                <i :class='item.class'></i><br>
                <span>{{item.name}}</span>
           </router-link >
       </ul>

</router-link >
    </div>
</template>

<script>
export default {
    name:'elmfooter',
    data () {
        return {
            menu:[
                {name:'外卖',class:'iconfont icon-changyonglogo40',href:"/home"},
                {name:'搜索',class:'iconfont icon-zhinanzhen',href:"/search"},
                {name:'订单',class:'iconfont icon-dingdan',href:"/order"},
                {name:'我的',class:'iconfont icon-wode',href:"/user"},
            ],
            isActive:false,
        }
    },
    beforeRouteEnter(to,from,next){
        console.log("你好")
    },
    methods: {


    }
}
</script>

<style scoped>
.elm-footer{
    position: fixed;
    bottom:0;
    left:0;
    width:100%;
    background: #fff;
    z-index: 9999;
}
.elm-footer ul{
    display: flex;
    justify-content: space-between;
    align-content: center;
    border-top:1px solid #ddd;
    padding-bottom:0.1rem;
}
.elm-footer ul li{
    width:25%;
    text-align: center;
    color:#555;
    font-size:0;
}
.elm-footer ul li i{
    font-size:0.81rem;
    color:#555;
}
.elm-footer ul li.active i{
    color:rgb(16, 166, 204);
}
.elm-footer ul li span{
    font-size:0.24rem;
}

</style>


