<template>
    <div class="user">
        <elm-header :fatherName="fatherName"></elm-header>
        <div class="user-head">
            <img src="../assets/img/hmj.jpg" alt="" />
            <div>
                <h1>xiaowu0371</h1>
                <p>15049405397</p>
            </div>
            <router-link class="iconfont icon-arrow-right-copy-copy" to="/account"></router-link>
        </div>
        <ul class="user-top">
            <li>
                <span><strong class="user-balance">{{$store.state.userInfor.userBalance}}</strong>元<br></span>
                <p>我的余额</p>
            </li>
            <li>
                <span><strong class="user-discount">{{$store.state.userInfor.userDiscount}}</strong>元<br></span>
                <p>我的优惠</p>
            </li>
            <li @click="sendIntegral">
                <span><strong class="user-integral">{{$store.state.userInfor.userIntegral}}</strong>元<br></span>
                <p>我的积分</p>
            </li>
        </ul>
        <div class="user-list">
            <ul>
                <li class="user-order" @click="sendOrder">
                    <i class="iconfont icon-dingdan"></i>
                    我的订单
                </li>                
                <li class="user-shop" @click="sendIntegral">
                    <i class="iconfont icon-gouwudai"></i>
                    积分商城
                </li>
                <li class="user-card" @click="sendVip">
                    <i class="iconfont icon-huiyuan"></i>
                    会员卡
                </li>

            </ul>
            <ul>
                <li class="user-service" @click="sendService">
                    <i class="iconfont icon-wode"></i>
                    服务中心
                </li>
                <li class="user-dwonload" @click="sendApp">
                    <i class="iconfont icon-changyonglogo40"></i>
                    下载APP
                </li>
            </ul>
        </div>
        <router-link to="/lx">测试</router-link>
        <elm-footer></elm-footer>
    </div>
</template>

<script>
    export default {
        name: "user",
        data() {
            return {
                fatherName: '我的',
            }
        },
        methods:{
            sendIntegral:function(){
                this.$router.push("/integral")
            },
            sendApp:function(){
                this.$router.push("/account/app")
            },
            sendOrder:function(){
                this.$router.push("/order")
            },
            sendService:function(){
                this.$router.push("/service")
            },
            sendVip:function(){
                this.$router.push("/vip")
            }
        }
    }
</script>

<style scoped>
    .user-head {
        display: flex;
        justify-content: space-between;
        align-items: center;
        background: rgb(16, 166, 204);
        padding: 0.3rem 0.2rem;
    }

    .user-head img {
        width: 1.2rem;
        height: 1.2rem;
        border-radius: 50%;
        margin-right:0.2rem;
    }
    .user-head>div{
        flex: 1;
    }
    .user-head h1 {
        font-size: 0.34rem;
        font-weight: bold;
        color: #fff;
    }

    .user-head p {
        font-size: 0.24rem;
        color: #fff;
    }

    .user-head a {
        display: block;
        width:0.3rem;
        color: #fff;
        font-size: 0.34rem;
    }
    .user-top{
        display: flex;
        justify-content: space-between;
        align-items: center;
        background: #fff;
    }
    .user-top li{
        flex:32% 0;
        text-align: center;
        color:#555;
        border-right:2px solid #ddd;
        padding:0.3rem 0 0.2rem;
    }
    .user-top li:last-child{
        border-right:none;
    }
    .user-top li span strong{
        font-size:0.5rem;
        font-weight: bold;
        padding-right:0.06rem;
    }
    .user-balance{
        color:red
    }
    .user-discount{
        color:orange;
    }
    .user-integral{
        color:rgb(39, 224, 39);
    }
    .user-list ul{
        margin-top:0.2rem;
        padding:0 0.3rem;
        background: #fff;
    }
    .user-list ul li{
        position: relative;
        padding:0.3rem 0;
        background: url(../../static/img/icon-arrow-right.png) no-repeat right center;
        background-size:0.3rem 0.3rem;
        color:black;
    } 
    .user-list ul li::after{
           position: absolute;
           bottom:0;
           left:0.4rem;
           content:"";
           width:7rem;
           height:1px;
           background:#ddd;
    }
      .user-list ul li:last-child::after{
          height:0;
      }
    .user-list ul li i{
        font-size:0.3rem;
        padding-right:0.1rem;
    }
    .user-list ul li.user-order i{
        color:rgb(59, 124, 245);
    }
    .user-list ul li.user-shop i{
        color:deeppink;
    }
    .user-list ul li.user-card i{
        color:gold;
    }
    .user-list ul li.user-service i{
        color:rgb(59, 124, 245);
    }
    .user-list ul li.user-dwonload i{
        color:rgb(59, 124, 245);
    }
</style>


