<template>
        <div class="account-password">
            <elm-header :fatherName="fatherName"></elm-header>
            <div class="account-password-main">
                <label for="oldPwd">旧密码：<input type="password" placeholder="请填写旧密码" v-model="oldPassword" @change="userInputOldPassword"/></label>
                <label class="account-password-newPWD" for="newPwd">新密码：<input type="password" placeholder="请填写新密码" v-model="newPassword" /></label>
                <p>密码长度8~32位，需包含数字、字符、符号最少两种或以上元素</p>
                <button class="account-password-button" @click="sendChanged">确认</button>
            </div>
        </div>
    </template>
    
    <script>
    import { mapState } from "vuex"
    export default{
        name:"accountPassword",
        data () {
            return {
                fatherName:"修改密码",
                newPassword:null,
                oldPassword:null,
            }
        },
        computed: mapState(['userInfor']),
        methods: {
            sendChanged:function(){
                this.$router.push("/account");
            },
            userInputOldPassword:function(){
                if(this.oldPassword!==this.userInfor.userPassword){
                    alert("请输入正确的密码")
                }else{
                    alert("密码输入正确，请输入您要修改的密码！")
                }
            }
        }

    }
    
    </script>

<style scoped>
.account-password-main{
    padding:2rem 0.8rem 0;
}
.account-password label{
    display: block;
    width:100%;
    border-bottom:1px solid #999;
    padding:0.3rem 0;
    font-size:0.3rem;
    color:black;
    font-weight: bold;
}
.account-password input{
    border:none;
    outline: none;
    background: transparent;
    font-weight: normal;
}
.account-password-newPWD{
    margin-top:0.5rem;
}
.account-password p{
    margin-top:0.2rem;
}
.account-password-button{
    width:100%;
    background: #10a6cc;
    outline:none;
    border:none;
    color:#fff;
    font-size: 0.36rem;
    padding:0.2rem 0;
    border-radius: 0.5rem;
    margin-top:1rem;

}
</style>


