<template>
    <div class="pay">
            <elm-header :fatherName="fatherName"></elm-header>    
        <div class="pay-main">
                <div class="pay-top">
                        <p class="pay-top-time">剩余支付时间：{{payTime}}</p>
                        <p class="pay-money">￥{{userInfor.totalPrice}}</p>
                        <router-link class="pay-introduce" to="/payment">麦当劳&nbsp;订单详情<i class="icon-arrow-right-copy-copy iconfont"></i></router-link> 
                </div>
                <ul @click="chooseItem">
                    <li >
                        <img src="../assets/img/elm.jpg" alt="" />
                        <span>饿了么</span>
                        <input type="radio" value="饿了么">
                    </li>
                    <li>
                        <img src="../assets/img/wx.jpg" alt="" />
                        <span>微信</span>
                        <input type="radio" value="饿了么">
                    </li>
                    <li>
                        <img src="../assets/img/zfb.jpg" alt="" />
                        <span>支付宝</span>
                        <input type="radio" value="饿了么">
                    </li>
                </ul>
                <mt-button type="primary" size="large" @click="pay">确认支付</mt-button>
        </div>
    
    </div>
</template>

<script>
import { mapState } from "vuex"
export default{
    name:"pay",
    data () {
        return {
            fatherName:"订单支付",
            payTime:"14:20",
            options:[],
            payChoose:null
        }
    },
    computed: mapState(['userInfor']),
    methods: {
        chooseItem:function(event){
            //console.log(event.target)
            if(event.target.className){
                event.target.className="";
            }else{
                event.target.className="active iconfont icon-duihao";
            }
        },
        pay:function(){
            alert("不好意思，该功能暂时开通不了！")
        }
    }

}

</script>


<style scoped>
html,body{
    height:100%;
}
.pay{
    height:100%;
    background: #fff;
    padding-bottom:0.3rem;
}
.pay-main{
    padding: 0.3rem 0.2rem;
}
.pay-top{
    text-align: center;
}
.pay-top-time,.pay-introduce{
    font-size:0.26rem;
    color:#999;
}
.pay-money{
    font-size:0.8rem;
    color:black;
    font-weight: bold;
}
.pay-main ul li{
    display: flex;
    align-items: center;
    justify-content: space-between;
    padding:0.6rem 0.3rem;
    border-bottom: 1px solid #f7f7f7;
}
.pay-main ul li img{
    width:0.36rem;
    margin-right:0.2rem;
}
.pay-main ul li span{
    flex: 1;
    text-align: left;
    font-size:0.4rem;
    color:black;
}
.pay-main ul li input{
    width:0.4rem;
    height:0.4rem;
    border-radius: 100%;
    border:1px solid #999;
    color:#fff;
    text-align: center;
}
.pay-main ul li input.active{
    border:1px solid transparent;
    background: orange;
}






.pay-button{
    width:95%;
    margin:1rem auto;
}



</style>


