<template>
    <div class="invoice">
        <elm-header :fatherName="fatherName"></elm-header>
        <div class="invoice-main">
            <p class="invoice-type">
                <span>类型：</span>
                <label class="active" for="" @click="addActive"><input type="radio" value="" />公司</label>
                <label for="" @click="addActive"><input type="radio" value=""  />个人</label>
                <label for="" @click="addActive"><input type="radio" value=""  />非企业单位</label>
            </p>
            <mt-field label="抬头" placeholder="请填写准确的抬头名称" v-model="rise" ></mt-field>
            <mt-field label="税号" placeholder="请填写【税号】或社会信用代码" v-model="dutyParagraph"></mt-field>
        </div>
        <div class="invoice-button">
            <p>可咨询公司财务部分获得【税号】或社会信用代码</p>
            <mt-button type="primary" size="large" @click="sendInvoiceTitle">保存</mt-button>
        </div>
    </div>
</template>

<script>
import { mapState,mapMutations } from 'vuex'
export default{
    name:"invoice",
    data () {
        return {
            fatherName:"新增发票信息",
            isactive:false,
            rise:null,
            dutyParagraph:"",
        }
    },
    computed: mapState(['userOrderInvoice']),
    methods:{
        ...mapMutations(['Add_Invoice']),
        addActive:function(event){
            var invoiceTitleList=Array.from(document.getElementsByTagName("label"));   //返回为类数组，需要转换数组才能使用数组方法
            invoiceTitleList.forEach(element => {
                element.classList.remove("active");
            });
            event.target.classList.add("active");
        },
        sendInvoiceTitle:function(){
            this.$router.push("/payment");
        }
    },
    watch: {
        rise:function(val){
            this.Add_Invoice(val);
        }
    }
}

</script>


<style lang="scss" type="text/scss" >

.invoice-main{
    padding:0 0.2rem;
    background:#fff;
    overflow: hidden;
}
.invoice-type{
    display:flex;
    align-items: center;
    padding:0.3rem 0 0.3rem 0.2rem;
}
.invoice-type>span{
    width:0.8rem;
}
.invoice-type label{
    padding:0.08rem 0.4rem;
    border:1px solid #e6e6e6;
    margin-right:0.2rem;
}
.invoice-type label.active{
    border:1px solid #0eb8e2;
    background: rgba(163, 228, 245, 0.3);
}
.invoice-button{
    padding:0 0.2rem;
    text-align: center;
}
.invoice-button p{
    padding:0.3rem 0 0.6rem 0;
    color:#999;
}
 /*  首先修改scoped重写修改成lang="scss" type="text/scss" */
 .invoice .mint-field .mint-cell-title{
    width:1rem;  
}
</style>


