<template>
    <div class="payment">
        <elm-header :fatherName="fatherName"></elm-header>
        <div class="payment-address">
            <span class="payment-address-icon iconfont icon-ai-cart"></span>
            <p><strong>{{userChooseAddress.name}}</strong>{{userChooseAddress.sex}}{{userChooseAddress.tel}}</p>
            <p><span class="payment-address-home">{{userChooseAddress.marker}}</span>{{userChooseAddress.content}}</p>
            <span class="payment-address-modify iconfont icon-arrow-right-copy-copy" @click="activeSheet"></span>
        </div>
        <div class="payment-time">
            <strong>送达时间</strong>
            <div>
                <p>尽快送达|预计{{time}}</p>
                <p class="payment-time-fn">蜂鸟转送</p>
            </div>
        </div>
        <div class="payment-methods">
            <p>
                <span class="payment-methods-title">支付方式</span>
                <span>在线支付<i class="iconfont icon-arrow-right-copy-copy"></i></span>
            </p>
            <p>
                <span>红包</span>
                <span>暂时只在饿了么App上支持</span>
            </p>
        </div>
        <div class="payment-main">
            <h1><img :src="restaurantList[RestaurantId].img" alt="" />{{restaurantList[RestaurantId].title}}</h1>
            <ul>
                <li v-for="(item, index) in carMenu" :key="index">
                    <span class="payment-main-food">{{item.name}}</span>
                    <span class="payment-main-food-number">×{{showChooseCount(item)}}</span>
                    <span class="payment-main-foot-price">￥{{(typeof item.price === "object")?item.price[index].minPrice:item.price}}</span>
                </li>
            </ul>
            <p class="payment-main-delivery">
                <span>配送费</span>
                <span>￥{{restaurantList[RestaurantId].delivery}}</span>
            </p>
            <p class="payment-main-total">
                    <span >订单<i class="">￥{{userInfor.totalPrice+restaurantList[RestaurantId].delivery}}</i></span>
                    <span >待支付</span>
            </p>
        </div>
        <div class="payment-main-remarks">
            <p>
                <span class="">订单备注</span>
                <span>{{userOrderRemarks}}</span>
                <router-link to="/remarks" class="iconfont icon-arrow-right-copy-copy"></router-link>
            </p>
            <p>
                <span>发票抬头</span>
                <span>{{userOrderInvoice}}</span>
                <router-link to="/invoice" class="iconfont icon-arrow-right-copy-copy"></router-link>
            </p>
        </div>       

        <mt-popup position="bottom" v-model="addressMenuVisible">
                <div class="payment-address-set">
                    <h1><span @click="AddressSetClose">取消</span>选择收货地址</h1>
                    <ul>
                        <li v-for="(item,index) in userInfor.address" @click="AddressChoose(item,index)">
                            <span v-if="isAddressChoose==index" class="iconfont icon-duihao"></span>
                            <div>
                                    <h2>{{item.content}}<span>{{item.marker}}</span></h2>
                                    <p>{{item.name}}&nbsp;&nbsp;{{item.tel}}</p>
                            </div>
                            <span class="payment-address-set-modify" @click="ModifyAddress(index)">修改</span>
                        </li>
                    </ul>   
                    <router-link to="/addAddress" class="payment-address-add"><i class=" iconfont icon-tianjia"></i>添加</router-link>
                </div>
        </mt-popup>

        <p class="payment-footer">
            <span class="payment-footer-number">待支付<i class="">￥{{userInfor.totalPrice+restaurantList[RestaurantId].delivery}}</i></span>
            <span class="payment-footer-sure" @click="pay">确认下单</span>
        </p>
    </div>
</template>


<script>
import {mapState,mapMutations} from "vuex";
export default {
    name:"payment",
    data () {
        return {
            fatherName:"确认订单",
            time:"15:52",
            payNumber:425,
            choeesNumber:1,
            price:34,
            isShow:false,
            isAddressChoose:null,           
            addressMenuVisible:false,    
            userChooseAddress:{
                marker:"",
                content:"",
                tel:"",
                name:"",
                sex:"",
            },
            carMenu:[],
            RestaurantId:0,
        }
    },
    created (){
        this.RestaurantId=this.$route.query.RestaurantId;
        this.carMenu=Array.from(new Set(this.$store.state.carMenu)).filter(filterData);
        function filterData(item){
            return item.chooseCount!==0;
        }
        this.userChooseAddress=this.userInfor.address[this.userInfor.address.length-1];
        this.isAddressChoose=this.userInfor.address.length-1;
    },
    mounted () {
        
    },
    computed: mapState({
            userInfor:"userInfor",
            restaurantList:'restaurantList', //餐馆
            userOrderRemarks:'userOrderRemarks',
            userOrderInvoice:'userOrderInvoice',
            //carMenu:"carMenu", //购物车
            showChooseCount:function(){
                return function(item){
                if(item.chooseCount){
                    return item.chooseCount;
                }else{
                    console.log(item)
                    let chooseItem=item.price.filter(function(item){
                        return item.chooseCount!==0;
                    })
                    console.log(chooseItem)
                    return chooseItem[0].chooseCount;
                }
                }
            }
    }),
    methods: {
        activeSheet:function(){
            this.addressMenuVisible=true;
            //console.log(this.addressMenuVisible)
        },
        AddressSetClose:function(){
            this.addressMenuVisible=false;
        },
        AddressChoose:function(o,i){
            this.isAddressChoose=i;
            //console.log(i)
            this.userChooseAddress=o;

        },
        pay:function(){
            this.$router.push("/pay");
        },
        ModifyAddress:function(index){
            this.$router.push({
                name:"addAddress",
                params:{
                    id:index+1
                }
            })
        }
    },
    watch: {
        'userInfor.address':function(val){
            this.userChooseAddress=val[val.length-1];
        },
       
    }
}


</script>

<style scoped>
.payment-address{
    position: relative;
    padding:0.3rem 1rem;
    background: #fff;
}
.payment-address>p{
    font-size:0.28rem;
    line-height: 1.5em;
}
.payment-address>p strong{
    font-size:0.36rem;
    font-weight: bold;
}
.payment-address-home{
    display: inline-block;
    font-size:0.2rem;
    color:#fff;
    padding:0 0.1rem;
    background: orange;
    border-radius: 0.06rem;
    margin-right:0.1rem;
    vertical-align: middle;
}
.payment-address-icon{
    position: absolute;
    font-size:0.4rem;
    color:rgb(13, 170, 243);
    left:0.3rem;;
    top:0.5rem;
}
.payment-address-modify{
    position: absolute;
    font-size:0.4rem;
    right:0.3rem;;
    top:0.5rem;
}
.payment-time{
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding:0.3rem;
    margin-top:0.14rem;
    background: #fff;
    border-left:0.08rem solid rgb(13, 170, 243);
}
.payment-time>strong{
    font-size:0.36rem;
    font-weight: bold;
}
.payment-time>div{
    text-align: right;
}
.payment-time>div p{
    font-size:0.3rem;
    color:rgb(13, 170, 243);
    letter-spacing: 0.02rem;
}
.payment-time>div p.payment-time-fn{
    display: inline-block;
    text-align: right;
    margin-top:0.1rem;
    color:#fff;
    font-size:0.24rem;
    background: rgb(13, 170, 243);
    padding:0 0.1rem;
    border-radius:0.06rem;
}
.payment-methods{
    margin-top:0.14rem;
    padding:0 0.3rem;
    background: #fff;
}
.payment-methods>p{
    display: flex;
    justify-content: space-between;
    padding:0.3rem 0.04rem;
}
.payment-methods>p:first-child{
    border-bottom:0.01rem solid rgb(241, 239, 239);
}
.payment-methods>p span{
    color:#999;
    letter-spacing: 0.02rem;
}
.payment-methods>p span.payment-methods-title{
    font-size:0.36rem;
    color:#111;
}
.payment-main{
    margin-top:0.14rem;
    background: #fff;
}
.payment-main>h1{
    padding:0.3rem 0.4rem;
    font-size:0.36rem;
    color:#555;
    border-bottom:0.01rem solid rgb(241, 239, 239);
    letter-spacing: 0.02rem;
} 
.payment-main>h1 img{
    width:1rem;
    height:1rem;
    padding-right:0.1rem;
    vertical-align: middle;
    border-radius: 0.1rem;
}
.payment-main>ul{
    padding:0.2rem 0.3rem;
}
.payment-main>ul li{
    display: flex;
    justify-content: space-between;
    padding:0.2rem 0;
}
.payment-main>ul li span{
    color:#555;

}
.payment-main-food{
    flex: 1;
}
.payment-main-food-number{
    width:1rem;
}
.payment-main>ul li span.payment-main-foot-price{
    width:1rem;
    text-align: right;
    color:orange;
}
.payment-main-delivery{
    display: flex;
    justify-content: space-between;
    padding:0.3rem;
    border-bottom:0.01rem solid rgb(241, 239, 239);
}
.payment-main-total{
    display: flex;
    justify-content: space-between;
    padding:0.3rem;
}
.payment-main-total span i{
    font-style: normal;
}
.payment-main-total span:last-child{
    color:orange;
}
.payment-main-remarks{
    margin:0.14rem 0 1.3rem;
    background: #fff;
    padding:0 0.3rem;
}
.payment-main-remarks>p{
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding:0.3rem 0;
}
.payment-main-remarks>p span:first-child{
    width:2rem;
}
.payment-main-remarks>p span:nth-child(2){
    flex:1;
    text-align: right;
    color:#999;
}
.payment-main-remarks>p span:last-child{
    width:0.5rem;
    text-align: right;
}
.payment-address-set{
    width:7.5rem;
    padding:0 0.3rem;
}
.payment-address-set h1{
    position: relative;
    padding:0.3rem;
    font-size:0.3rem;
    font-weight: bold;
    text-align: center;
    border-bottom:0.01rem solid rgb(241, 239, 239);
}
.payment-address-set h1 span{
    position: absolute;
    left:0;
    top:0.38rem;
    font-size:0.24rem;
    font-weight: normal;
}
.payment-address-set ul{
    height:7rem;
}
.payment-address-set ul li{
    position: relative;
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding:0.2rem 0 0.2rem 0.5rem;
    border-bottom:0.01rem solid rgb(241, 239, 239);
} 
.payment-address-set ul li div{
    flex:1;
}
.payment-address-set ul li h2{
    position: relative;
    font-size:0.36rem;
    font-weight: bold;
    text-align: left;
}
.payment-address-set ul li h2 span{

    padding:0 0.1rem;
    font-size:0.3rem;
    color:#fff;
    font-weight: normal;
    border-radius: 0.04rem;
}
.payment-address-set ul li .iconfont{
    position: absolute;
    left:0;
    top:50%;
    margin-top:-0.2rem;
    width:0.4rem;
    height:0.4rem;
    background: orange;
    border-radius: 50%;
    color:#fff;
}
.payment-address-set ul li .iconfont::before{
    position: absolute;
    left:0.06rem;
    top:0.06rem;
}
.payment-address-set-modify{
    width:0.6rem;
}
.payment-address-add{
    display: block;
    padding:0.3rem 0;
    border-top:0.01rem solid rgb(241, 239, 239);
    text-align: center;
    font-size:0.36rem;
}
.payment-address-add .iconfont{
    color:orange;
    font-size:0.4rem;
    padding-right:0.1rem;
}

.payment-footer{
    position: fixed;
    left:0;
    bottom:0;
    display: flex;
    justify-content:space-between;
    width:100%;
    background: #555;  
    padding-left:0.4rem; 
}
.payment-footer span{
    color:#fff;
    font-size:0.36rem;   
    padding:0.3rem 0;
    letter-spacing: 0.02rem;
}
.payment-footer-number{
    flex:1;
}
.payment-footer-number i{
    font-style: normal;
    letter-spacing: normal;
    padding-left:0.1rem;
}
.payment-footer-sure{
    width:2rem;
    background: rgb(39, 197, 39);
    text-align: center;
}

</style>


