<template>
    <div class="search">
        <elm-header :fatherName="fatherName"></elm-header>
        <div class="search-main">
            <input type="text" v-model="userInput" placeholder="请输入商家名或是美食名字"  name="userInput" @keyup.enter="send"/>
            <button @click="send">提交</button>
            <i class="mint-toast-icon mintui mintui-field-error search-reset" v-show="userInput" @click="searchReset"></i> 
        </div>
        <div v-show="clearHistory" class="search-history">
            <h1>搜索记录</h1>
            <p v-for='(item,index) in searchItems'> {{ item }}</p>
            <p v-for='(itemsession,index) in getsearcherItems'> {{ itemsession }}</p>
        </div>
        <p v-show="clearHistoryButton" class="clear-search-history" @click='clearSearch' >清空搜索历史</p>
        <elm-footer></elm-footer>
    </div>
</template>

<script>
    export default {
        name: "search",
        data() {
            return {
                fatherName: '搜索',
                userInput:null,
                isshow_reset:false,
                searchItems:[],
                getsearcherItems:[],
                clearHistory:false,
                clearHistoryButton:false,
            }
        },
        created(){

        },
        updated() {

        },
        
        beforeRouteEnter (to, from, next) {
            next(vm=>{ 
                if(window.localStorage.getItem("searchItems")){
                    var strSearchItem=window.localStorage.getItem("searchItems");
                    vm.getsearcherItems=strSearchItem.split(",");
                    if(vm.getsearcherItems.length>0){
                        vm.clearHistoryButton=true;
                        vm.clearHistory=true;
                    }
                } 
                //console.log(vm.getsearcherItems);  
            })   
        },
        beforeRouteLeave (to, from, next){
            //业务逻辑，进入页面前获取本地存储，转化成数组，将用户输入存入页面数组，当离开页面是把数组存入进入页面获取的本地存储数组里面，然后在吧该数组存入本地存储；
            if(this.searchItems.length!==0){
                this.getsearcherItems.unshift(this.searchItems);
            }
            this.searchItems=[];
            //console.log(this.arrs);     
            window.localStorage.setItem("searchItems",this.getsearcherItems); 
            //console.log(this.getsearcherItems);
            next();
        },
        methods: {
            searchReset:function(){
                this.userInput="";
            },
            send:function(){  
                if(this.userInput.length!==0){
                    this.searchItems.unshift(this.userInput);
                }
                this.clearHistoryButton=true;
            },
            clearSearch:function(){
                window.localStorage.setItem("searchItems",''); 
                this.clearHistoryButton=false;
                this.clearHistory=false;
                this.getsearcherItems=[];
                this.searchItems=[];

            }
        },
        watch: {
            searchItems:function(){
                if(this.searchItems.length!==0){
                    this.clearHistoryButton=true;
                    this.clearHistory=true;
                }
            }
        }

    }
</script>

<style scoped>
    .search-main{
        position: relative;
        display: flex;
        justify-content: space-between;
        align-items: center;
        padding:0.2rem;
        background: #fff;
    }
    .search-main input{
        flex:80%;
        line-height:0.6rem;
        border:1px solid #ddd;
        background: rgb(243, 243, 243);
        outline: none;
        margin-right:0.1rem;
        text-indent: 0.2rem;
    }
    .search-main button{
        flex:18%;
        height:0.6rem;
        background: rgb(16, 166, 204);
        outline: none;
        border:none;
        color:#fff;
    }
    .search-reset{
        position: absolute;
        right:23%;
        top:0.36rem;
        color:rgb(199, 197, 197);
    }
    .search-history{
        margin-top:0.1rem;
        background: #fff;
    }
    .search-history h1{
        font-size:0.28rem;
        color:#555;
        line-height:0.4rem;
        padding:0.2rem;
        background: rgb(243, 243, 243);
    }
    .search-history p{
        color:#555;
        line-height: 1.5em;
        padding:0.2rem 0.3rem;
        border:1px solid rgb(243, 243, 243);
    }
    .clear-search-history{
        font-size:0.3rem;
        font-weight: bold;
        color:rgb(16, 166, 204);
        line-height: 1.5em;
        padding:0.2rem 0.3rem;
        text-align: center;
        background: #fff;
    }
</style>


