<template>
    <div class="order">
        <elm-header :fatherName="fatherName"></elm-header>
        <h1 class="order-title">
            <span v-for="(item,index) in orderType" :key="index" @click="isshow=item">{{item}}</span>
        </h1>
        <div class="order-main">
            <template v-if="isshow=='全部订单'">
                <ul class="order-all">
                    <li class="order-list" v-for="item in orderList" :key="item.id">
                        <div class="order-all-restaurant">
                            <img :src="item.img" alt="" />
                            <h2>
                                <p class="order-all-restaurant-title">
                                    <span>{{item.title}}<i class="iconfont icon-arrow-right-copy-copy"></i></span>

                                    <span>{{item.state}}</span>
                                </p>
                                <p class="order-all-restaurant-discount">
                                    <span v-for="(discountList, index) in item.discount"
                                        :key="index">{{discountList}}</span>
                                </p>
                            </h2>
                        </div>
                        <ul class="order-all-foot">
                            <li v-for="(foot, footIndex) in item.orderMenu" :key="footIndex">
                                <div class="order-all-foot-title">
                                    <img :src="foot.img" alt="" />
                                    <h3>{{foot.title}}</h3>
                                </div>
                                <p class="order-all-foot-price">
                                    <span>共{{foot.number}}件</span>
                                    <span>￥{{foot.price}}</span>
                                </p>
                            </li>
                        </ul>
                        <p class="order-all-button"><button>再来一单</button></p>
                    </li>
                </ul>
            </template>
            <template class="order" v-if="isshow=='待评价'">
                <img class="order-none" src="../assets/img/order-01.png" alt="" />
                <p class="order-tips">奖励您一课爱心，您的所有订单已完成评价！</p>
            </template>
            <template v-if="isshow=='退款'">
                <img class="order-none" src="../assets/img/order-01.png" alt="" />
                <p class="order-tips">没有退款信息，谢谢您对我们的肯定。</p>
            </template>
        </div>
        <elm-footer></elm-footer>
    </div>
</template>

<script>
    import { mapState } from "vuex"
    export default {
        name: "order",
        data() {
            return {
                fatherName: '订单',
                msg: "您还没有订单信息，有喜欢的就赶快下手吧！",
                isshow: '全部订单',
                orderType: ["全部订单", "待评价", "退款"],
                orderList: [
                    {
                        id: 1,
                        title: "官氏翅吧（上地店）",
                        img: "https://xiaowu0371.github.io/static/img/restaurant/6.jpg",
                        discount: ["22减10"],
                        state: "骑手已取货",
                        orderMenu: [
                            {
                                title: "(招牌菜)特色烤鸡翅",
                                img: "https://xiaowu0371.github.io/static/img/foot/kjc.png",
                                number: 1,
                                price: '10',

                            }
                        ],
                    },
                    {
                        id: 2,
                        title: "杨国富麻辣烫（中关村店）",
                        img: "https://xiaowu0371.github.io/static/img/restaurant/3.jpg",
                        discount: ["22减10"],
                        state: "完成",
                        orderMenu: [
                            {
                                title: "女神套餐（娃娃菜+菠菜+土豆片+金针菇+香菇+午餐肉+鱼丸）",
                                img: "https://xiaowu0371.github.io/static/img/foot/mlt.png",
                                number: 1,
                                price: '28',

                            }
                        ],
                    }
                ]
            }
        },
        computed: mapState(['carMenu']),
    }
</script>

<style>
    .order-main {
        margin-bottom: 2rem;
    }

    .order-title {
        display: flex;
        justify-content: space-around;
        padding: 0.3rem 0;
        background: #fff;
    }

    .order-title span {
        font-size: 0.36rem;
        color: black;
    }

    .order-list {
        margin: 0.2rem;
        padding: 0.2rem;
        background: #fff;
        border-radius: 0.1rem;
    }

    .order-all-restaurant {
        display: flex;
        justify-content: space-between;
        align-items: center;
    }

    .order-all-restaurant img {
        width: 0.7rem;
        height: 0.7rem;
        margin-right: 0.2rem;
    }

    .order-all-restaurant h2 {
        flex: 1;
    }

    .order-all-restaurant-title {
        display: flex;
        justify-content: space-between;
        align-items: center;
    }

    .order-all-restaurant-title span {
        font-size: 0.36rem;
        font-weight: bold;
        color: black;
    }

    .order-all-restaurant-title i {
        font-size: 0.34rem;
        color: black;
        padding-left: 0.2rem;
    }

    .order-all-restaurant-title span:last-child {
        font-weight: normal;
        font-size: 0.28rem;
    }

    .order-all-restaurant-discount {
        margin-top: 0.06rem;
    }

    .order-all-restaurant-discount span {
        padding: 0 0.14rem;
        color: #fff;
        background: orangered;
        margin-right: 0.06rem;
    }

    .order-all-foot {
        margin-top: 0.5rem;
    }

    .order-all-foot-title {
        display: flex;
        align-items: center;
    }

    .order-all-foot-title img {
        width: 1.4rem;
        height: 1.2rem;
        border-radius: 0.08rem;
        margin-right: 0.2rem;
    }

    .order-all-foot-title h3 {
        flex: 1;
        font-size: 0.30rem;
        color: #999;
        letter-spacing: 0.02rem;
    }

    .order-all-foot-price {
        margin-top: 0.08rem;
        text-align: right;
    }

    .order-all-foot-price span:last-child {
        color: black;
        font-size: 0.28rem;
    }

    .order-all-button {
        margin-top: 0.2rem;
        text-align: right;
    }

    .order-all-button button {
        background: transparent;
        border: 1px solid #999;
        font-size: 0.26rem;
        padding: 0.08rem 0.2rem;
    }

    .order-none {
        display: block;
        width: 60%;
        height: auto;
        margin: 1rem auto 0;
        opacity: 0.3;
    }

    .order-tips {
        text-align: center;
        font-size: .3rem;
        color: #999;
        letter-spacing: 0.04rem;
        margin-top: 0.2rem;
    }
</style>


