<template>
    <div class="address">
        <elm-header :fatherName="fatherName"></elm-header>
        <div class="address-items">
            <mt-field label="联系人"  v-model="addAddress.name" placeholder="您的姓名"></mt-field>
            <mt-field label="联系电话" type="tel" v-model="addAddress.tel" placeholder="您的电话"></mt-field>
            <mt-field label="送餐地址" v-model="addAddress.content" placeholder="详细地址（如门牌号等）"></mt-field>
            <mt-field label="标签"  v-model="addAddress.marker" placeholder="家/公司/学校"></mt-field>
        </div>
        <mt-button class="address-button" type="primary" size="large" @click="addItem(addAddress)">确定</mt-button>

    </div>
</template>

<script>
//import { mapMutations } from "vuex"
export default{
    name:"addAddress",
    data () {
        return {
            fatherName:"添加地址",
            addAddress:{
                marker:null,
                content:null,
                tel:null,
                name:null,
                sex:null,
                default:false,
            },

        }
    },
    created () {
        if(this.$route.params.id){
            let i=this.$route.params.id;
            this.addAddress=this.$store.state.userInfor.address[i-1];
        }
        
    },
    methods: {
       // ...mapMutations(['Add_address']),
        addItem:function(obj){
            if(!this.$route.params.id){
                this.$store.commit("Add_addressItem",obj)
            }
            this.$router.push("/payment");    
        },

    },

}

</script>

<style scoped>
.address-items{
    background: #fff;
}
.mint-field-core{
    color:#fff;
}
.address-button{
    width:96%;
    margin:0.3rem auto 0;
}


</style> 


