<template>
    <div class="location-city">
        <elm-header :fatherName="fatherName" :cityPage="cityPage">
            <router-link to="/" class="location-city-change">切换城市</router-link>
        </elm-header>
        <div class="location-city-input">
            <input id="city-userInput" type="text" v-model="userLocation"  placeholder="请输入您的具体地址" />
            <button @click="sendRestaurant">提交</button>
        </div>
        <div></div>
        <div v-if="citySearchHistory" class="location-city-search">
            <p>搜索历史</p>
            <dl>
                <dt>中关村软件园</dt>
                <dd>北京市海淀区</dd>
                <dt>中关村软件园</dt>
                <dd>北京市海淀区</dd>
                <dt>中关村软件园</dt>
                <dd>北京市海淀区</dd>
                <dt>中关村软件园</dt>
                <dd>北京市海淀区</dd>
            </dl>
            <h1>清空所有</h1>
        </div>

    </div>
</template>

<script>
export default{
    name:"locationCity",
    data() {
        return {
            fatherName:null,
            cityPage:true,
            userLocation:null,
            userAutocomplete:null,
            citySearchHistory:false,
        }
    },
    //模板渲染钱调用，dom元素还没有挂载
    created () {
      this.fatherName=this.$route.query.id;
    },
    //挂载dome元素，初始化数据
    mounted() {
        var $this=this;
         //放在created里面会报错
         var ac = new BMap.Autocomplete({
                "input": "city-userInput"
            })
            
            //ac.addEventListener('onhighlight', function(e) { })// 鼠标放在下拉列表上的事件
            ac.addEventListener("onconfirm", function (e) {  //鼠标点击下拉列表
                //console.log(e.item.value)
                $this.userAutocomplete = e.item.value.business;
                console.log($this.userAutocomplete)
            })
    },
    updated() {
         //console.log(this.userAutocomplete)
    },
    methods: {
        sendRestaurant(){
            var value=null;
            if(this.userAutocomplete){
                value=this.userAutocomplete;
            }else{
                value=this.userLocation;
            }
            if (value) {
                this.$router.push({
                    path: "/home",
                    query: {
                        id: value
                    }
                })
            }else{
                alert("请输入您的具体地址！")
            }
            
        }

        
    }
}

</script>


<style scoped>
.location-city a.location-city-change{
    font-size:0.3rem;
}
.location-city-input{
    padding:0.2rem 0.4rem ;
    background: #ffffff;
}
#city-userInput{
    display: block;
    width:100%;
    height:0.6rem;
    border:1px solid #ddd;
    padding-left:0.2rem;
}
.location-city-input button{
    display: block;
    width:100%;
    padding:0.16rem 0;
    border:none;
    background: #10a6cc;
    margin-top:0.2rem;
    color:#fff;
    font-size:0.3rem;
}
.location-city-search{
}
.location-city-search p{
    font-size:0.24rem;
    padding:0.1rem 0.2rem;
}
.location-city-search dl{
    background: #fff;
    padding:0 0.3rem;
}
.location-city-search dl dt{
    font-size:0.3rem;
    color:#000;
    padding-top:0.3rem;
}
.location-city-search dl dd{
    font-size:0.22rem;
    color:#999;
    padding:0.2rem 0;
    border-bottom:1px solid #ebebeb;
}
.location-city-search h1{
    font-size:0.36rem;
    padding:0.2rem 0;
    text-align: center;
    color:#000;
    background: #fff;
}
</style>


