<template>
    <div class="account">
        <elm-header :fatherName="fatherName"></elm-header>
        <ul class="account-list">
            <li class="account-picture">
                <span>头像</span>
                <span><img :src="imgURL" alt="" /></span>
                <i class="iconfont icon-arrow-right-copy-copy" to="" @click="sheetVisible=!sheetVisible"></i>
                <input class="account-header-upload-btn" type="file" name="image" accept="/image/*" @change="onchangeImgFun">

            </li>
            <li>
                <span>用户名</span>
                <span>{{userInfor.userName}}</span>
                <router-link class="iconfont icon-arrow-right-copy-copy" to="/account/name"></router-link>
            </li>
            <li>
                <span>账号密码</span>
                <span>修改</span>
                <router-link class="iconfont icon-arrow-right-copy-copy" to="/account/password"></router-link>
            </li>
            <li>
                <span>手机号</span>
                <span>150****5397</span>
                <router-link class="iconfont icon-arrow-right-copy-copy" to="/account/tel"></router-link>
            </li>
            <li v-if="bindWeiXin">
                <span>微信账号</span>
                <span>解绑</span>
                <i class="iconfont icon-arrow-right-copy-copy" @click="unbindWeiXin"></i>
            </li>
            <li>
                <span>注销账号</span>
                <span>注销后无法恢复，请谨慎操作</span>
                <router-link class="iconfont icon-arrow-right-copy-copy" to="/account/cancel"></router-link>
            </li>
            </ul>
            <p class="account-signOut">退出当前账号</p>
    </div>
</template>
<script>
import { MessageBox } from 'mint-ui'
import { mapState } from 'vuex'
export default{
    name:"account",
    data () {
        return {
            fatherName:"我的账号",
            bindWeiXin:true,
            actions:[
                {
                    name:"拍照",
                    method:function(){

                    }
                },
                {
                    name:"从相册中选择",
                    method:function(){
                        console.log(this.name)
                    }
                }
            ],
            sheetVisible:false,
            imgURL:require('../../../static/img/tx.jpg'),


        }
    },
    computed: mapState(['userInfor']),
    methods: {
        //这种方式只能调用相册，不能控制相册打开方式，如果做混合开发的话，可以使用cordova插件做壳子；
        onchangeImgFun: function (e) {
            var file = e.target.files[0];
            if(!file){
                return
            }
            var size = (file.size / 1024).toFixed(2);  //获取图片大小，单位是b
            var maxSize = 2048;//设置图片上传最大值为2M
            var type = file.type;
            var $this = this;
            if (type !== "image/png" && type !== "image/jpeg") {
                alert("图片上传只支持JPG/PNG格式,请重新上传！");
                return
            }
            //如果图片大于2m，压缩图片，
            var reader = new FileReader();
            reader.readAsDataURL(file);  //把读取的结果保存到result里面；
            reader.onloadend = function () {
                var dataURL = this.result;
                var img=new Image();
                img.src=this.result;
                img.onload = function () {
                    if (size > maxSize) {
                        var img64 = this.compressImg(img, 100, 100, 0.7);
                        //this.upLoadImg(img64);
                    }
                    //uploadImg(result);
                    $this.imgURL = dataURL;  //预览图片
                } 
            }

        },
        unbindWeiXin:function(){
            MessageBox.confirm('确 定 要 接 触 微 信 绑 定 吗 ?').then(action => {
                this.bindWeiXin=false;
            })
        },
        compressImg(img,width,height,ratio){
            let canvas,ctx;
            canvas=document.createdElement("canvas");
            canvas.width=width;
            canvas.height=height;
            ctx=canvas.getContext("2d");
            ctx.drawImage(img,0,0,width,height);
            return canvas.toDataURL("image/jpeg",ratio);
        },
        upLoadImg(img){
            let params={imgPath:img};
            this.$axios({
                url:"xxxxxxx",  //上传接口
                data:params,   
                type:"post",
                success:function(data){
                    if(data.code==1){
                        var imgPath = data.imgPath;//假设接口返回的地址为imgPath
                    //$("#img").attr("src", imgPath);实现图片预览；
                    }
                }

            })
        }

    }

}

</script>

<style scoped>
.account-list{
    margin-top:0.2rem;
    background:#fff;
    padding:0 0.3rem;
}
.account-list li{
    position: relative;
    display: flex;
    width:100%;
    align-items: center;
    justify-content: space-between;
    border-bottom:1px solid #f3f1f1;
    padding:0.3rem 0;
}
.account-list li span{
    color:#999;
    font-size:0.26rem;
}
.account-list li span:first-child{
    flex:1;
    width:80%;
    color:#555;
}

.account-list li a,.account-list li i{
    color:#999;
    font-size:0.36rem;
    margin-left:0.2rem;
}
.account-picture img{
    width:0.8rem;
    height:0.8rem;
    border-radius: 100%;
}
.account-signOut{
    position: fixed;
    bottom:0;
    left:0;
    width:100%;
    padding:0.3rem 0;
    text-align: center;
    background:#fff ;
    font-size:0.36rem;
    color:rgb(240, 72, 72);
}
.account-header-upload-btn{
    position: absolute;
    right:0;
    top:0.2rem;
    z-index: 999;
    font-size:0;
    width:0.5rem;
    height:1.1rem;
    border:1px solid red;
    opacity: 0;
}

</style>


