<template>
    <div class="location">
        <elm-header :fatherName="fatherName"></elm-header>
        <div class="location-title">
            <p class="title-remarks">
                <span>当前城市定位：</span>
                <span>定位不准确时，请在城市列表选择</span>
            </p>
            <p class="user-location" @click="sendCity(locationCity)">
                <span>{{locationCity}}</span>
                <i class="iconfont icon-arrow-right-copy-copy"></i>
            </p>
        </div>
        <dl class="hot-citys">
            <dt>热门城市</dt>
            <dd v-for="(hotCitysItem, index) in hotCitys" :key="index" @click="sendCity(hotCitysItem)">{{hotCitysItem}}</dd>
        </dl>
        <dl v-for="(CitysItem, index) in citys" :key="index">
            <dt>{{CitysItem.n}}</dt>
            <dd v-for="(item, index) in CitysItem.c" :key="index" @click="sendCity(item.n)">{{item.n}}</dd>
            
        </dl>
    </div>
</template>

<script type="text/javascript">
export default{
    name:"location",
    data() {
        return {
            fatherName:"饿了么",
            hotCitys:["北京","哈尔滨","上海","南京","杭州","广州","深圳","重庆"],
            citys:null,
            locationCity:null,
        }
    },
    created () {
        var myCity = new BMap.LocalCity();
            var $this=this;
            myCity.get(function (result) {
                return $this.locationCity=result.name;
            });
    },
    mounted() {
        this.getCitys();
    },
    computed: {

    },
    methods: {
        getCitys(){
            this.$axios.get("https://xiaowu0371.github.io/static/json/citys.json").then((re)=>{  //build时路径需要转换
                this.citys=re.data.citys;
            })
        },
        sendCity(city){
            this.$router.push({
                path:"/city",
                query:{
                    id:city
                }
            })
        },
    }
}

</script>

<style scoped>
.location-title{
    background: #fff;
}
.location-title p{
    height:0.9rem;
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding:0 .3rem;
    border-bottom:1px solid #ebebeb;
}
.title-remarks span{
    font-size:0.24rem;
}
.user-location span,i{
    font-size:0.38rem;
    color: #10a6cc;
}
.location dl{
    margin-top:0.3rem;
    background: #fff;
    font-size:0;

}
.location dl dt{
    font-size:.32rem;
    padding:.3rem .3rem;
    border-bottom:1px solid #ebebeb;
}
.location dl dd{
    display: inline-block;
    padding:.26rem 0.1rem;
    width:25%;
    border:1px solid #ebebeb;
    text-align: center;
    overflow:hidden;
    text-overflow:ellipsis;
    white-space:nowrap;
}
.location dl.hot-citys dd{
    font-size:0.3rem;
    color:#10a6cc;
}






</style>


