import axios from "axios"

const Get_Data="Get_Data"
const Get_Address="Get_Address"

export default{
    [Get_Data](context){
        axios.get("https://xiaowu0371.github.io/static/json/restaurant-menu.json")
        .then(re=>{
            context.commit("Get_Data",re)
        })
        .catch(error=>{
            console.log(error)
        })
    },
    [Get_Address]({commit}){
        axios.get("https://www.easy-mock.com/mock/5b4972414ddc075ff3d24f53/example/address")
        .then(re=>{
            commit("Get_Address",re)
        })
        .catch(error=>{
            console.log(error)
        })
    }
}



