<template>
    <div class="account-name">
        <elm-header :fatherName="fatherName"></elm-header>
        <div class="account-name-main">
                <h1>用户名：</h1>
                <input type="text"  v-model="newName" />
                <p>以中文或英文字母开头，限4-16个字符，一个汉字为两个字符</p>
                <button class="account-name-button"  @click="sendChanged">确定</button>
        </div>
    </div>
</template>

<script>
import { mapState,mapMutations } from 'vuex'
export default{
    name:"accountName",
    data () {
        return {
            fatherName:"修改用户名",
            newName:null,
        }
    },
    computed:mapState(['userInfor']),
    methods: {
        ...mapMutations(['Change_UserName']),
        sendChanged:function(){
            this.$router.push("/account")
        }
    },
    watch: {
        newName:function(val){
            this.Change_UserName(val);
        }
    }
}
</script>

<style scoped>
.account-name-main {
    padding:0 0.3rem;
}
.account-name h1{
    font-size: 0.3rem;
    padding:0.2rem 0 0.5rem;
    color:black;
}
.account-name input{
    width:100%;
    background: transparent;
    border:none;
    border-bottom:1px solid #10a6cc;
    outline: none;
    color:black;
}
.account-name p{
    padding-top:0.3rem;
    color:#333;
}
.account-name-button{
    width:100%;
    background: #10a6cc;
    outline:none;
    border:none;
    color:#fff;
    font-size: 0.36rem;
    padding:0.2rem 0;
    border-radius: 0.5rem;
    margin-top:0.4rem;
}
</style>


