<template>
    <div class="restaurant-item">
        <div class="restaurant-item-header">
            <span class="restaurant-item-header-bg"></span>
            <div class="restaurant-item-top">
                <img :src="restaurantList[RestaurantId].img" alt="" />
                <div>
                    <h1>{{restaurantList[RestaurantId].title}}</h1>
                    <p>
                        <span>商家配送/</span>
                        <span>40分钟送达/</span>
                        <span>配送费/{{restaurantList[RestaurantId].delivery}}</span>
                    </p>
                    <p>欢迎光临：用餐高峰期请提前下单，谢谢。</p>
                </div>
                <router-link class="iconfont icon-arrow-right-copy-copy" to="/restaurant-intr"></router-link>
            </div>
            <p class="restaurant-item-header-mask">
                <span class="restaurant-item-new">新</span>
                <span>新用户下单立减20元（不予其他活动共享）（APP专享）</span>
                <span></span>
            </p>
        </div>
        <div class="restaurant-item-main">
            <h1>
                <span class="restaurant-menu-name">{{restaurantMenuName}}</span>
                {{restaurantMenuTtile}}
            </h1>
            <ul class="restaurant-item-menu">
                <li :class="{active:isActive==index}" v-for="(item,index) in restaurantMenu" :key="index"
                    @click="addActive(index)">
                    {{item.name}}
                    <span></span>
                </li>
            </ul>
            <ul class="restaurant-item-content">
                <template v-for="(items,index) in restaurantMenu">
                    <template v-for="(item,index) in items.menu">
                        <li @click="sendFood(index)">
                            <img :src="item.img" alt="" />
                            <div class="restaurant-item-content-main">
                                <h1>{{item.name}}<span class="restaurant-item-news">新品</span></h1>
                                <p>
                                    <span>月销售{{item.salesVolume}}份</span>
                                    <span>好评率{{item.evaluation}}</span>
                                </p>
                                <p class="restaurant-item-content-bottom">
                                    <span class="restaurant-item-price"
                                        ref="itemPrice">￥{{(typeof item.price === "object")?item.price[0].minPrice:item.price}}<i
                                            v-if="typeof item.price === 'object'">起</i></span>
                                    <span class="restaurant-item-price-specs" v-if="typeof item.price === 'object'"
                                        @click.stop="chooseSetMeal(item)">选规格
                                        <i class="restaurant-item-price-specs-count" v-show="item.chooseCount">{{item.chooseCount}}</i>
                                        <span v-if="chooseSetMealShow" class="chooseSetMeal-box">
                                            <span class="chooseSetMeal">
                                                <span class="chooseSetMeal-title">{{chooseSetMealObj.name}}</span>
                                                <span class="chooseSetMeal-list">规格</span>
                                                <i v-for="(option,i) in chooseSetMealObj.price"
                                                    :class="{active:(isChooseSetMeal==i)}"
                                                    @click.stop='chooseSetMealOption(i)'>{{option.title}}</i>
                                                <span
                                                    class="chooseSetMeal-chooseItem">已选择规格：{{chooseSetMealObj.price[isChooseSetMeal].title}}</span>
                                                <span class="chooseSetMeal-total">
                                                    <span><i>套餐单价</i>￥{{chooseSetMealObj.price[isChooseSetMeal].minPrice}}</span>
                                                    <span>
                                                        <i v-if="!chooseSetMealObj.price[isChooseSetMeal].chooseCount"
                                                            @click.stop="addItem(chooseSetMealObj,chooseSetMealObj.price,isChooseSetMeal);{chooseSetMealObj.price[isChooseSetMeal].chooseCount++}">加入购物车</i>
                                                        <template v-else>
                                                            <i class="iconfont"
                                                                @click.stop="reduceItem(chooseSetMealObj,chooseSetMealObj.price,isChooseSetMeal);{chooseSetMealObj.price[isChooseSetMeal].chooseCount--}">—</i>
                                                            <i
                                                                class="chooseItemCount">{{chooseSetMealObj.price[isChooseSetMeal].chooseCount}}</i>
                                                            <i class="iconfont icon-tianjiajiahaowubiankuang"
                                                                @click.stop="addItem(chooseSetMealObj,chooseSetMealObj.price,isChooseSetMeal);{chooseSetMealObj.price[isChooseSetMeal].chooseCount++}"></i>
                                                        </template>
                                                    </span>
                                                </span>
                                                <span class="chooseSetMeal-close"
                                                    @click.stop="chooseSetMealClose(chooseSetMealObj)">close</span>
                                            </span>
                                        </span>
                                    </span>
                                    <span v-else>
                                        <i v-if="item.chooseCount" class="iconfont"
                                            @click.stop="reduceItem(item,item.price);{item.chooseCount--}">—</i>
                                        <i v-if="item.chooseCount" class="chooseItemCount">{{item.chooseCount}}</i>
                                        <i class="iconfont icon-tianjiajiahaowubiankuang"
                                            @click.stop="addItem(item,item.price);{item.chooseCount++}"></i>
                                    </span>
                                </p>

                            </div>
                        </li>
                    </template>
                </template>


            </ul>

        </div>
        <div class="restaurant-item-footer">
            <p>
                <i class="iconfont icon-ai-cart">
                    <span class="cart-number">{{userInfor.carNumber}}</span>
                </i>
                <span class="discount-price">￥{{userInfor.totalPrice}}<i class="price">￥{{userInfor.totalPrice}}</i></span><br>
                <span class="price-mark">配送费 ￥{{restaurantList[RestaurantId].delivery}} </span>
            </p>
            <p @click="toPayment(RestaurantId)">去结算</p>
        </div>


    </div>
</template>

<script>
    import { mapState, mapMutations, mapGetters } from "vuex"

    export default {
        name: "restaurantItem",
        data() {
            return {
                fatherName: "",
                isActive: false,
                restaurantMenuName: "热销榜",
                restaurantMenuTtile: "没有什么比人气更能说明问题。",
                menu: null,
                isChooseSetMeal: 0,
                chooseSetMealShow: false,
                chooseSetMealObj:{},
                chooseSetMealNmuber:0,
                RestaurantId:null,
            }
        },
        created() {
            if(this.$store.state.restaurantMenu.length===0){
                this.$store.dispatch("Get_Data");
            }
            this.RestaurantId=this.$route.params.id;
        },
        computed: {
            ...mapState({
            restaurantList:'restaurantList', //餐馆
            restaurantMenu:'restaurantMenu', //菜单
            userInfor:'userInfor', //总价格
            carMenu:"carMenu", //购物车
            })
        },
        methods: {
            ...mapMutations(["Add_CarNumber","Reduce_CarNumber"]),
            sendFood:function(index){
                this.$router.push("/food")
            },
            addActive: function (i) {
                this.isActive = i;
                this.restaurantMenuName = this.restaurantMenu[i].name;
                this.restaurantMenuTtile = this.restaurantMenu[i].title;
            },
            chooseSetMeal: function (item) {
                //console.log(item)
                this.chooseSetMealShow = true;
                //必须存储item，因为vue组件的生命周期，”选规格“ 内部接收不到item；
                //浅拷贝，chooseSetMealObj的数值变化，item也会跟着变化；
                this.chooseSetMealObj=item;
            },
            chooseSetMealClose: function (obj) {
                this.chooseSetMealShow = false;
                console.log(this.chooseSetMealObj)
                var total=0;
                for(let i=0;i<this.chooseSetMealObj.price.length;i++){
                   total+=parseInt(this.chooseSetMealObj.price[i].chooseCount);
                   console.log(total)
                }
                obj.chooseCount=total;
            },
            addItem: function (o, p, i) {                 
                this.Add_CarNumber({
                    option:o,
                    price:p,
                    index:i,
                });  
    
            },
            reduceItem: function (o, p, i) {
               // console.log(this.$store.state.carMenu);
                this.Reduce_CarNumber({
                    option:o,
                    price:p,
                    index:i,
                });  
                
            },
            chooseSetMealOption: function (i) {
                this.isChooseSetMeal = i;
                //console.log(this.isChooseSetMeal)
            },
            toPayment:function(id){
                if(this.$store.state.userInfor.carNumber===0){
                    alert("您还没有选择任何菜品！")
                    return
                }
                this.$router.push({
                    path:"/payment",
                    query:{
                        RestaurantId:id
                    }
                });
            }
        },

    }

</script>



<style scoped>
    .restaurant-item-header {
        position: fixed;
        left: 0;
        top: 0;
        width: 100%;
        height: 2.4rem;
        background: rgba(88, 88, 88, 0.9);
        z-index: 9999;
        padding: 0.2rem;
        overflow: hidden;
    }

    .restaurant-item-header-bg {
        position: absolute;
        left: 0;
        top: 0;
        width: 100%;
        height: 2.4rem;
        background: url(../assets/img/hmj.jpg) no-repeat;
        background-size: cover;
        z-index: -1;
        filter: blur(0.2rem);
    }

    .restaurant-item-top {
        position: relative;
        display: flex;
        justify-content: space-between;
        align-items: center;
        z-index: inherit;
    }

    .restaurant-item-top img {
        width: 1.5rem;
        height: 1.5rem;
        border-radius: 0.08rem;
        margin-right: 0.2rem;
    }

    .restaurant-item-top div {
        flex: 1;
    }

    .restaurant-item-top h1 {
        font-size: 0.4rem;
        color: #fff;
        letter-spacing: 0.02rem;
        padding-bottom: 0.1rem;
    }

    .restaurant-item-header p {
        color: #fff;
        font-size: 0.2rem;
        letter-spacing: 0.02rem;
        line-height: 1.5em;
    }

    .restaurant-item-header p>span {
        font-size: 0.2rem;
        letter-spacing: 0.02rem;
    }

    .restaurant-item-header .iconfont {
        display: block;
        width: 0.3rem;
        text-align: right;
        font-size: 0.5rem;
        color: #fff;
    }

    .restaurant-item-header-mask {
        margin-top: 0.2rem;
    }

    .restaurant-item-new {
        padding: 0.06rem;
        background: rgb(39, 197, 39);
        border-radius: 0.06rem;
    }

    .restaurant-item-main {
        position: relative;
        top: 2.4rem;
        background: #fff;
        height: 13rem;
        padding-left: 1.8rem;
    }

    .restaurant-item-main>h1 {
        padding: 0.3rem 0.2rem;
        line-height: 0.3rem;
        background: rgb(253, 252, 252);
        font-size: 0.22rem;
    }

    .restaurant-menu-name {
        font-size: 0.36rem;
        font-weight: bold;
    }

    .restaurant-item-content {
        background: #fff;

    }

    .restaurant-item-content li {
        display: flex;
        justify-content: space-between;
        padding: 0.3rem 0.2rem 0.3rem 0.3rem;
        border-bottom: 0.04rem solid rgb(253, 252, 252);
    }

    .restaurant-item-content li img {
        width: 1.3rem;
        height: 1.3rem;
        margin-right: 0.2rem;
    }

    .restaurant-item-content li .restaurant-item-content-main {
        flex: 1px;
    }

    .restaurant-item-content li h1 {
        font-size: 0.3rem;
        font-weight: bold;
        color: black;
        white-space: nowrap;
        text-overflow: ellipsis;
        overflow: hidden;
    }

    .restaurant-item-content li p {
        font-size: 0.22rem;
        margin-top: 0.08rem;
    }

    .restaurant-item-content li p span {
        padding-right: 0.1rem;
    }

    .restaurant-item-content-bottom {
        display: flex;
        justify-content: space-between;
        align-items: center;
    }

    .restaurant-item-content li p .restaurant-item-price {
        font-size: 0.3rem;
        font-weight: bold;
        color: orangered;

    }

    .restaurant-item-content li p .restaurant-item-price i {
        font-style: normal;
        font-size: 0.2rem;
        padding-left: 0.1rem;
    }

    .restaurant-item-content li p .restaurant-item-price-specs {
        position: relative;
        font-size: 0.26rem;
        line-height: 0.3rem;
        padding: 0.1rem 0.2rem;
        background: #14c9f7;
        color: #fff;
        border-radius: 0.06rem;
    }

    .restaurant-item-content li p span i.restaurant-item-price-specs-count{
        position: absolute;
        right:-0.1rem;
        top:-0.2rem;
        display: inline-block;
        width:0.4rem;
        height:0.4rem;
        border-radius: 50%;
        background: orangered;
        border-radius: 50%;
        font-style: normal;
        text-align: center;
        line-height: 0.4rem;
    }

    .restaurant-item-content li p .iconfont {
        text-align: center;
        line-height: 0.26rem;
        font-size: 0.22rem;
        padding: 0.08rem;
        font-weight: bold;
        border-radius: 50%;
        background: rgb(20, 201, 247);
        color: #fff;
    }

    .restaurant-item-content li p .chooseItemCount {
        font-size: 0.26rem;
        padding: 0 0.1rem;
        font-style: normal;
    }

    .restaurant-item-content .restaurant-item-news {
        display: block;
        position: absolute;
        left: -0.24rem;
        top: -0.16rem;
        color: #fff;
        font-size: 0.16rem;
        transform: rotate(-30deg);
        padding: 0.1rem 0.6rem 0.04rem 0.3rem;
        background: rgb(39, 197, 39);
    }

    .chooseSetMeal-box {
        position: fixed;
        left: 0;
        top: 0;
        display: block;
        width: 100%;
        height: 100%;
        background: rgba(0, 0, 0, 0.3);
    }

    .chooseSetMeal {
        position: fixed;
        left: 50%;
        ;
        top: 50%;
        margin-top: -2.5rem;
        margin-left: -3.4rem;
        display: block;
        width: 6.8rem;
        height: 4rem;
        background: #fff;
        padding: 0.4rem 0.3rem;
        box-sizing: border-box;
        border-radius: 0.1rem;
    }

    .chooseSetMeal-title {
        display: block;
        font-size: 0.3rem;
        font-weight: bold;
        color: #333;
    }

    .chooseSetMeal-list {
        display: block;
        color: #555;
        padding-top: 0.4rem;
        font-size: 0.22rem;
    }

    .chooseSetMeal i {
        display: inline-block;
        color: #333;
        padding: 0.1rem 0.16rem;
        border: 1px solid #999;
        border-radius: 0.08rem;
        margin: 0.2rem;
        font-style: normal;
    }

    .chooseSetMeal i.active {
        border: 1px solid orange;
    }

    .chooseSetMeal-chooseItem {
        display: block;
        font-size: 0.22rem;
        color: #555;
        margin-top: 0.4rem;
    }

    .chooseSetMeal-total {
        display: flex;
        justify-content: space-between;
        color: red;
        font-size: 0.36rem;
        font-weight: bold;
    }

    .chooseSetMeal-total i {
        border: none;
        font-size: 0.3rem;
        color: #333;
        font-weight: bold;
        padding: 0 0;
    }

    .chooseSetMeal-close {
        position: absolute;
        right: 0.3rem;
        top: 0.3rem;
        color: #555;
        font-size: 0.28rem;
    }









    .restaurant-item-menu {
        position: fixed;
        left: 0;
        top: 2.4rem;
        width: 1.8rem;
        background: rgb(253, 252, 252);
    }

    .restaurant-item-menu li {
        padding: 0.4rem 0.2rem;
        border-bottom: 1px solid rgb(243, 241, 241);
    }

    .restaurant-item-menu li.active {
        position: relative;
        background: #fff;
        font-size: 0.3rem;
    }

    .restaurant-item-menu li.active::before {
        position: absolute;
        left: 0;
        top: 0;
        content: "";
        width: 0.04rem;
        height: 1.2rem;
        background: rgb(20, 201, 247);
    }



    .restaurant-item-footer {
        position: fixed;
        left: 0;
        bottom: 0;
        display: flex;
        justify-content: space-between;
        align-items: center;
        height: 1.1rem;
        width: 100%;
        background: #555;
        z-index: 999;
    }

    .restaurant-item-footer p {
        font-size: 0.3rem;
        color: #fff;
    }

    .restaurant-item-footer p:first-child {
        padding-left: 1.6rem;
    }

    .restaurant-item-footer .discount-price {
        font-size: 0.4rem;
        /* line-height: 0.3rem; */

    }

    .restaurant-item-footer .price {
        font-size: 0.24rem;
        color: #999;
        text-decoration: line-through;

    }

    .restaurant-item-footer .price-mark {
        display: block;
        font-size: 0.24rem;
        color: #999;
        line-height: 0.3rem;
        margin-top: -0.04rem;

    }

    .restaurant-item-footer .iconfont {
        display: block;
        position: absolute;
        left: 0.3rem;
        top: -0.2rem;
        width: 1.1rem;
        height: 1.1rem;
        background: #333;
        border-radius: 50%;
        text-align: center;
        line-height: 1.1rem;
        font-size: 0.5rem;
        z-index: 2;
    }

    .restaurant-item-footer .iconfont::after {
        position: absolute;
        left: 0.08rem;
        top: 0.08rem;
        content: "";
        width: 0.94rem;
        height: 0.94rem;
        background: rgb(20, 201, 247);
        border-radius: 50%;
        z-index: -1;
    }

    .restaurant-item-footer .cart-number {
        position: absolute;
        right: 0;
        top: 0;
        padding: 0 0.14rem;
        background: orangered;
        font-size: 0.26rem;
        border-radius: 0.2rem;
        line-height: 0.4rem;
        text-align: center;
    }

    .restaurant-item-footer p:last-child {
        width: 2rem;
        text-align: center;
        background: rgb(39, 197, 39);
        font-size: 0.3rem;
        line-height: 0.31rem;
        padding: 0.4rem 0;
    }
</style>


