const Get_Data = "Get_Data"
const Add_CarNumber = "Add_CarNumber"
const Reduce_CarNumber = "Reduce_CarNumber"
const Get_Address="Get_Address"
const Add_addressItem="Add_addressItem"
const Choose_OrderRemarks="Choose_OrderRemarks"
const Add_Invoice="Add_Invoice"
const Change_UserName="Change_UserName"


export default {
    [Get_Data](state, re) {
        //console.log(re.data)
        state.restaurantMenu = re.data.restaurantMenu;
    },
    [Get_Address](state, re) {
        state.userInfor.address = re.data.restaurantMenu;
    },
    //添加地址
    [Add_addressItem](state,obj){
        state.userInfor.address.push(obj);
    },
    [Add_Invoice](state,str){
        state.userOrderInvoice=str;
    },
    [Choose_OrderRemarks](state,arr){
        if(arr.join){
            state.userOrderRemarks=arr.join(",");
        }else{
            state.userOrderRemarks=arr;
        }
        
        console.log(state.userOrderRemarks)
    },
    [Add_CarNumber](state, obj) {
        if (typeof obj.price === "object") {
            state.userInfor.totalPrice += parseInt(obj.price[obj.index].minPrice);
        } else {
            state.userInfor.totalPrice += parseInt(obj.price);
        }
        state.userInfor.carNumber++;
        state.carMenu.push(obj.option);
        //console.log(state.carMenu);
    },
    [Reduce_CarNumber](state, obj) {
        if (state.userInfor.carNumber > 0) {
            state.userInfor.carNumber--;
        } else {
            state.userInfor.totalPrice = 0;
            state.userInfor.carNumber = 0;
        }
        if (typeof obj.price === "object") {
            state.userInfor.totalPrice -= parseInt(obj.price[obj.index].minPrice);
        } else {
            state.userInfor.totalPrice -= parseInt(obj.price);
        }
    },
    [Change_UserName]:function(state,str){
        state.userInfor.userName=str;
    }
}







