<template>
        <div class="account-tel">
            <elm-header :fatherName="fatherName"></elm-header>
            <div class="account-tel-main">
                <label class="account-tel-oldtel"><span>旧手机号：</span><input type="text" placeholder="填写旧的手机号码" /><button>获取验证码</button></label>
                <label class="account-tel-newTel" >验证码：<input type="text" placeholder="请填写新密码" /></label>
                <button class="account-tel-button" @click="sendChanged">验证手机后更新新的号码</button>
            </div>
        </div>
    </template>
    
    <script>
    export default{
        name:"accountTel",
        data () {
            return {
                fatherName:"修改电话号码",
            }
        },
        methods: {
            sendChanged:function(){
                this.$router.push("/account");
            }
        }

    }
    
    </script>

<style scoped>
.account-tel-main{
    padding:2rem 0.8rem 0;
}
.account-tel label{
    display: block;
    width:100%;
    border-bottom:1px solid #999;
    padding:0.3rem 0;
    font-size:0.3rem;
    color:black;
    font-weight: bold;
}
.account-tel input{
    border:none;
    outline: none;
    background: transparent;
    font-weight: normal;
}
.account-tel .account-tel-oldtel{
    display: flex;
    justify-content: space-between;
    align-items: center;
}
.account-tel .account-tel-oldtel span{
    width:1.5rem;
}
.account-tel .account-tel-oldtel button{
    width:1.6rem;
    padding:0.1rem 0;
}
.account-tel-oldtel input{
    flex:1;
    width:2rem;
}
.account-tel-newTel{
    margin-top:0.5rem;
}
.account-tel p{
    margin-top:0.2rem;
}
.account-tel button{
    background: #10a6cc;
    outline:none;
    border:none;
    color:#fff;
}
.account-tel-button{
    width:100%;
    font-size: 0.36rem;
    padding:0.2rem 0;
    border-radius: 0.5rem;
    margin-top:1rem;

}
</style>


